import axios from "axios";

export function PostUserData(data){
    return axios.post(`http://localhost:3002`, data)
}
export function GetUserData(){
    return axios.get(`http://localhost:3002/i`);
}

export function PutUserData(id,data){
    return axios.put(`http://localhost:3002/book/${id}`,data);
}
export function PostCustomer(data){
    return axios.post(`http://localhost:3002/customer`, data)
}
export function GetCustomer(){
    return axios.get(`http://localhost:3002/customer`);
}
export function GetHall(id){
    return axios.get(`http://localhost:3002/${id}`);
}

export function GetCus(id){
    return axios.get(`http://localhost:3002/customerget/${id}`);
}
export function PutCus(id,data){
    return axios.put(`http://localhost:3002/customerput/${id}`, data)
}